import mycv from './cv-kimou.pdf';
import "../styles/about.css";
import { motion } from 'framer-motion';
import { Reveal } from '../Reveal';

function About() {
    return (
        <motion.div 
            id='about'
            variants={Reveal('up', 0.2)}
            initial="hidden"
            whileInView="show"
            viewport={{ once: false, amount: 0.7 }}
        >
            <div className="about-me">
                <h2>A propos</h2>
                <h6>Qui suis-je</h6>
                <div className="paragraph-me">
                    <p>
                        Amoureux de l'informatique depuis le lyce, j'ai dcid de me lancer dans ce domaine aprs l'obtention de mon bac, en particulier dans la conception de logiciels. Mes dbuts ont t prilleux, sems d'obstacles, mais je n'ai pas abandonn. J'ai tabli des objectifs ambitieux pour mes premires annes d'tudes. Actuellement en Licence 3 Informatique, j'ai dvelopp plusieurs comptences en algorithmique et en technologie. Aprs avoir consolid ces acquis, j'ai dcid de me tourner vers la data science pour la suite de mon parcours acadmique.
                    </p>
                </div>
                <div className="my-cv">
                    <a href={mycv} download>Tlcharger mon cv</a>
                </div>
            </div>
        </motion.div>
    );
}

export default About;
