import PictureCan from '../assets/can.png';
import PictureOtakuverse from '../assets/otakuverse.png';
import PictureSchool from '../assets/kimou-school.png';
import PortfolioPicture from '../assets/portfolio.png';
import PictureHitech from '../assets/hitech.png';
import { motion } from 'framer-motion';
import { Reveal } from '../Reveal';

import '../styles/projects.css';
function Projects(){
    return(
        <motion.div id="project"
            variants={Reveal('down', 0.4)}
            initial="hidden"
            whileInView="show"
            viewport={{ once: false, amount: 0.5 }}
        >
            <h2>Mes Projets</h2>
            <div className="grid-project">
                <div className="projects">
                    <div className="picture-project">
                        <img src={PictureCan} alt="" />
                    </div>
                    <div className="title-project">
                        <h3>ALLCAN</h3>
                    </div>
                    <div className="describe">
                        <p>
                            ALLCAN est une plateforme web qui dcrit l'historique des diffrentes CAN qui se sont drouls au fil du temps.
                            J'ai utilis PYTHON pour rcuprer les donnes et JAVASCRIPT pour les affichs.
                        </p>
                    </div>
                </div>
                <div className="projects">
                    <div className="picture-project">
                        <img src={PictureOtakuverse} alt="" />
                    </div>
                    <div className="title-project">
                        <h3>OTAKUVERSE</h3>
                    </div>
                    <div className="describe">
                        <p>
                            OtakuVerse est un site dynamique o les amateurs d'anims et de mangas se retrouvent pour partager leurs passions. <span style={{fontWeight:900}}>(la mise en ligne est pour bientt)</span>.
                            J'ai utilis PHP pour le backend et HTML CSS et JAVASCRIPT pour le front. Ici jAVASCRIPT joue un rle trs important avec sa capacit d'autamatiser les fonctionnalits.
                        </p>
                    </div>
                </div>
                <div className="projects">
                    <div className="picture-project">
                        <img src={PictureSchool} alt="" />
                    </div>
                    <div className="title-project">
                        <h3>KIMOU SCHOOL</h3>
                    </div>
                    <div className="describe">
                        <p>
                        IUTEA SCHOOL est une plateforme web PHP permettant aux tudiants de pourvoir noter leurs profs.Cette plateforme dispose d'une partie administrer par un admin c'est lui qui est charg d'enregistrer les tudiants et leurs fournis leurs mdp. Il est charg aussi d'enregsitrer les profs.
                        </p>
                    </div>
                </div>
                <div className="projects">
                    <div className="picture-project">
                        <img src={PortfolioPicture} alt="" />
                    </div>
                    <div className="title-project">
                        <h3>KIMOU PORTFOLIO</h3>
                    </div>
                    <div className="describe">
                        <p>
                            KIMOU PORTFOLIO est site web illustrant mon parcours de programmeur,mes objectifs,mes comptences et mes projets.Purement fait avec REACT JS 
                        </p>
                    </div>
                </div>
                <div className="projects">
                    <div className="picture-project">
                        <img src={PictureHitech} alt="" />
                    </div>
                    <div className="title-project">
                        <h3>HITECH SERVICES</h3>
                    </div>
                    <div className="describe">
                        <p>
                            HITECH SERVICES est une entreprise de maintenance tlphonique et informatique. Le site dispose d'une partie qui prsente l'entreprise et les services, et d'une autre qui est un ct e-commerce.
                            Je me suis occup de la partie backend avec PHP et un peu de JS pour l'automatisation des tches.
                            <a href="https://high-tech.ci/index.html" target="_blank" rel="noopener noreferrer">Consultez le site</a>.
                        </p>
                    </div>
                </div>
            </div>
        </motion.div>
    )
}

export default Projects;